import { ChangeDetectionStrategy, ChangeDetectorRef, Component, NgZone } from "@angular/core";


import { Platform } from "ionic-angular";
import { App } from "ionic-angular";
import { NavController } from "ionic-angular";
import { NavParams } from "ionic-angular";

import { AppConstants } from "@app/app.constants";
import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";

import { MarsAuthService } from "@services/auth.service";
import { MarsInteractionService } from "@services/interaction.service";
import { MarsNavigationService } from "@services/navigation.service";

import { Backend } from "@backend/index";


@Component({
    selector: "page-promotion-items",
    templateUrl: "promotion-items.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class PromotionItemsPage {

    navigationService: MarsNavigationService;
    translations: AppTranslations;

    items: Array<any>;
    category: string;
    categoryInfo: any;
    promotion: any;
    promotionId: string;
    promotionOptions: any;
    inventory: Array<any>;
    inventoryOptions: any;
    token: string;
    noItemsFound: boolean;
    merchant: string;
    canEdit: boolean;
    productsList = "inventory";
    isFirstItemAdded: boolean;
    FIRST_PROMOTION_ITEM_ADDED = "first_promotion_item_added";

    constructor(private platform: Platform,
        private app: App,
        private navCtrl: NavController,
        private navParams: NavParams,
        private changeDetector: ChangeDetectorRef,
        private zone: NgZone,
        private locales: AppLocales,
        private globals: AppGlobals,
        private authService: MarsAuthService,
        private interactionService: MarsInteractionService) {
        this.navigationService = new MarsNavigationService(this.app);
        this.navigationService.setNavCtrl(this.navCtrl);
        this.translations = this.locales.load();
    }

    ionViewCanEnter() {
        return this.navigationService.authCheck(PromotionItemsPage.name, ['merchant']);
    }

    async ionViewWillEnter() {
        if (!MarsAuthService.hasRole('MERCHANT')) return;
        if (MarsAuthService.isLoggedIn()) {
            this.category = this.navParams.get("category");
            this.promotionId = this.navParams.get("promotionId");
            this.token = MarsAuthService.getMarsToken();
            this.merchant = MarsAuthService.getLoggedInUser()._id;
            await this.getMarketItems();
            this.productsList = this.canEdit ? "inventory" : "promotion";
            this.configureMarketItemComponent();
        }
    }

    configureMarketItemComponent() {
        this.promotionOptions = {
            showPriceLabel: true,
            showPrice: true,
            showDiscountPriceInput: this.canEdit,
            showDiscountPrice: !this.canEdit,
            showPicture: true
        };
        this.inventoryOptions = {
            showPriceLabel: true,
            showPrice: true,
            showDiscountPriceInput: this.canEdit,
            showDiscountPrice: !this.canEdit,
            showPicture: true
        };
        this.changeDetector.detectChanges();
    }

    async getMarketItems() {
        try {
            let promotion = (await Backend.getPromotionItems({ merchant: this.merchant, id: this.promotionId, category: this.category })).data;
            this.categoryInfo = (await Backend.getInventoryItems({ merchant: this.merchant, category: this.category })).data;
            this.noItemsFound = !(this.categoryInfo.items && this.categoryInfo.items.length > 0);
            this.promotion = promotion.items;
            this.canEdit = this.isEditable(promotion);
            this.filterLists(this.categoryInfo.items, this.promotion);
        } catch (e) {
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            this.changeDetector.detectChanges();
        }
    }

    isEditable(promotion) {
        return promotion && promotion.editable;
    }

    view(item) {
        this.navigationService.goTo("MarketItemDetailsPage", {
            slug: item.information.slug
        });
    }

    async save(product) {
        try {
            product.loading = true;
            this.isFirstItemAdded = this.authService.getLoggedInUser().skip && !this.authService.getLoggedInUser().skip[this.FIRST_PROMOTION_ITEM_ADDED];
            let promotion = (await Backend.addPromotionItem({ category: this.category, id: this.promotionId, xAccessToken: this.token, item: product })).data;
            this.filterLists(this.categoryInfo.items, promotion.items);
            this.onFirstItemAdded();
            this.interactionService.toast(this.translations.operation_finished_succesfully);
        } catch (e) {
            this.changeDetector.detectChanges();
            product.loading = false;
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            this.changeDetector.detectChanges();
        }
    }

    async remove(product) {
        try {
            product.loading = true;
            let promotion = (await Backend.removePromotionItem({ category: this.category, id: this.promotionId, xAccessToken: this.token, item: product })).data;
            this.filterLists(this.categoryInfo.items, promotion.items);
            this.interactionService.toast(this.translations.operation_finished_succesfully);
        } catch (e) {
            product.loading = false;
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            this.changeDetector.detectChanges();
        }
    }

    filterLists(inventory, promotion) {
        this.promotion = promotion;
        let filteredInventory = [];
        inventory.map((item) => { // In case the item exists in the promotion, leave it out of inventory
            if (promotion.indexOfObject("inventoryRef._id", item._id) == -1) filteredInventory.push(Object.assign({}, item));
        });
        this.inventory = filteredInventory;
    }

    async onFirstItemAdded() {
        /*   this.interactionService.customAlert("congrats balanced", `
          <section class="congrats-title">
              ${this.translations.congrats}
          </section> 
          <section class="congrats-icon"></section>
          ${this.translations.first_item_added_to_promotion}`);
          try {
              this.isFirstItemAdded = false;
              await Backend.addSkip({ xAccessToken: this.token, skip: this.FIRST_PROMOTION_ITEM_ADDED });
          } catch (e) {
              
          } finally{
              this.changeDetector.detectChanges();
          } */
    }
}



